.btn {
  background-color: var(--primarycolor);
  color: var(--text);
  border-radius: 5px;
  margin: 2rem auto;
  width: 300px;
  padding: 0.5rem;
  text-align: center;
}

.home-header {
  position: relative;
  display: flex;
  justify-content: center;
  height: 70vh;
  background: linear-gradient(rgba(0, 0, 0, 0.931),black,rgb(0, 0, 0))
}
.home-header img {
  width: 100%;
  height: 100%;
  opacity: .5;
  object-fit: cover;
}

.home-header__content {
  position: absolute;
  bottom: 40%;
  color: white;
}

.home-header__content__header {
  font-size: 2.5rem;
  font-weight: bold;
}

.home-how_it_works {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 2rem 0;
  position: relative;
}

.home-cards-info {
  display: flex;
  justify-content: center;
  align-items: center;
}

.home-cards-info__card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  border-radius: 15px;
  box-shadow: rgba(167, 167, 167, 0.486) 1px 0px 5px 1px;
  margin: 1rem;
  transition: .5s;
}

.home-cards-info__card:hover {
  box-shadow: rgba(105, 105, 105, 0.637) 2px 1px 5px 1px;
  transition: .5s;
}

.home-cards-info__card img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-radius: 15px 15px 0 0;
}

.home-cards-info__card_text {
  padding: 1rem 1rem;
}

.home-bikes {
  background-color: var(--gray);
}

.home-bikes_in {
  padding: 2rem 0;
}

.home-bikes_header {
  text-align: center;
  margin: 1rem 0;
}

.home-bikes_header h1 {
  font-size: 2rem;
}

.home-bikes__body {
  display: grid;
}

.slide-margin {
  margin-right: 1rem;
}

.section-hr {
  text-align: center;
  border: 1px solid var(--gray2);
  width: 4rem;
  margin: 0 auto;
}


.home-cities-cards {
  margin: 5rem 0;
}

.home-cities-cards_in {
  margin-top: 1rem;
}

.home-city-card {
  border-bottom: .5px solid var(--gray3);
  border-radius: 40px;
  padding: 20px 0px;
  text-align: center;
  margin-top: 1rem;
}

.home-cities-cards_header {
  margin-top: 1rem;
}


.rent-your-bike {
  color: white;
  background: linear-gradient(var(--black), var(--red));
}

.rent-your-bike_in {
  display: grid;
  grid-template-columns: 50% 50%;
  padding: 4rem 0;
}

.rent-your-bike_in__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
}

.rent-your-bike_in__text {
  margin: auto 0;
}

@media only screen and (max-width: 767px) {
  .home-header {
    height: 60vh;
  }

  .home-cards-info {
    flex-direction: column;
    padding: 1rem;
  }

  .home-bikes_in {
    padding: 1rem;
  }

  .rent-your-bike_in {
    grid-template-columns: 100%;
    padding: 1rem 1rem;
  }

  .rent-your-bike_in__text {
    margin: 1rem 0;
  }
}