.nav-links {
    list-style: none;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.nav-links li {
    margin: 1rem;
}

.nav-links a {
    color: var(--text);
    text-decoration: none;
    padding: .1rem 0.2rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
    color: var(--black);
    font-weight: 500;
    transition: .3s;
}

.nav-links button {
    cursor: pointer;
    border: 1px solid #292929;
    color: #292929;
    background: transparent;
    padding: 0.5rem;
}

.nav-links button:focus {
    outline: none;
}

.nav-links button:hover,
.nav-links button:active {
    background: #292929;
    color: black;
}

@media (min-width: 768px) {
    .nav-links {
        flex-direction: row;
    }

    .nav-links li {
        margin: 0 0.5rem;
    }

    .nav-links a {
        color: var(--text);
        text-decoration: none;
    }

    .nav-links button {
        border: 1px solid var(--text);
        color: var(--white);
        background: var(--text);
    }

    .nav-links button:hover,
    .nav-links button:active {
        background: #f8df00;
        color: #292929;
    }
}